.navbar {
  background-color: transparent;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: black;
  width: 350px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  padding: 0 10px;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  text-decoration: none;
  color: black;
  font-size: 18px;

  justify-content: space-around;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: black;
  width: 30%;
  height: 10vh;
  justify-content: flex-start;
  align-items: center;
  margin-left: auto;
}

.nav-menu-accordion {
  margin-top: 5vh;
}

.nav-menu-accordion span {
  height: 4.5vh;
  font-size: larger;
}

@media only screen and (min-width: 1800px) {
  .nav-menu {
    width: 420px;
  }
  .nav-menu-accordion span {
    height: 4.5vh;
    font-size: x-large;
  }
}

@media only screen and (max-width: 768px) {
  .nav-menu {
    width: 100vw;
    z-index: 9999;
  }
  .nav-menu-accordion span {
    height: 4.5vh;
    font-size: x-large;
  }
}

.accordion-header-desc {
  padding: 0.25rem;
  font-size: 25px;
  display: flex;
  align-items: center;
}

.accordion-header-desc svg {
  width: 1.5em;
  height: 1.5em;
  padding-right: 2px;
}

.close-button svg {
  width: 30px;
  height: auto;
}

.vl {
  border-left: 3px solid black;
  height: 100%;
}

tr {
  border: solid;
  border-width: 1px;
}

th {
  border: solid;
  border-width: 1px;
}

.earthQuakeInfo {
  pointer-events: none;
}

.pinSvg {
  color: red;
  width: 4vw;
  height: 40vh;
}

.mapboxgl-marker {
  /* pointer-events: none; */
}

.tooltip {
  display: absolute;
  flex-direction: column;
  position: absolute;
  margin: 8px;
  padding: 4px;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  max-width: 40vw;
  font-size: 0.9em;
  z-index: 9999;
  pointer-events: none;
  opacity: 1;
}

.tooltip-properties {
  display: flex;
  flex-direction: row;
}

.tooltip-properties-human {
  display: flex;
  flex-direction: column;
  margin-right: 2vh;
  justify-content: space-between;
}

.tooltip-properties-building {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.map-wrapper {
  position: relative;
  transition: margin-left 0.5s;
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
}

.collapse-menu-button {
  position: absolute;
  top: 35px;
  left: 35px;
  width: 80px;
  height: 50px;
  background-color: black;
  border-color: white;
}

.collapse-menu-button:hover {
  border-color: white;
}

.collapse-menu-button-span {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.collapse-menu-button-span-svg {
  height: auto;
  width: 30px;
}

.row > * {
  padding-right: 0;
  padding-left: 0;
}

.vis {
  visibility: hidden;
}

.mapboxgl-ctrl-top-right {
  width: 10vw;
  height: 2vh;
  top: 3.5vh;
}

.map-control {
  top: 100px;
  right: 10px;
  position: absolute;
  margin: 0;
  display: flex;
  flex-direction: column;
  width: 32px;
}

.map-control > * {
  margin-top: 5px;
  position: relative !important;
}

.scaleControlStyle {
  bottom: 36px;
  left: 0;
  padding: 10px;
  font-size: 20px !important ;
}

